<template>
  <div class="container">
    <!-- 固定导航 -->
    <van-nav-bar fixed left-arrow @click-left="$router.back()" title="小智同学" class="page-nav-bar"></van-nav-bar>

    <!-- 聊天主体区域 -->
    <div class="chat-list">
      <div v-for="(obj, index) in list" :key="index">
        <!-- 左侧是机器人小思 -->
        <div class="chat-item left" v-if="obj.name !== 'me'">
          <van-image fit="cover" round src="https://img.yzcdn.cn/vant/cat.jpeg" />
          <div class="chat-pao">{{ obj.msg }}</div>
        </div>

        <!-- 右侧是当前用户 -->
        <div class="chat-item right" v-else>
          <div class="chat-pao">{{ obj.msg }}</div>
          <van-image fit="cover" round :src="$store.state.avatar" />
        </div>
      </div>
    </div>

    <!-- 对话区域 -->
    <div class="reply-container van-hairline--top">
      <van-field v-model="word" placeholder="说点什么...">
        <template #button>
          <span style="font-size: 12px; color: #999" @click="sendFn">提交</span>
        </template>
      </van-field>
    </div>
  </div>
</template>

<script>
// 1. 下包, 引入io
import { io } from 'socket.io-client'
export default {
  name: 'ChatIndex',
  data() {
    return {
      word: '', // 输入框的内容
      list: [
        // 所有的聊天消息
        // 只根据 name 属性，即可判断出这个消息应该渲染到左侧还是右侧
        { name: 'xs', msg: 'hi，你好！我是值' },
        { name: 'me', msg: '我是编程小王子' },
        { name: 'xs', msg: 'hi，你好！我大致' },
        { name: 'me', msg: '我是编程小王子' },
        { name: 'xs', msg: 'hi，你好！我是四肢' },
        { name: 'me', msg: '我是编程小王子' },
        { name: 'xs', msg: 'hi，你好！我是后置' },
        { name: 'me', msg: '我是编程小王子' }
      ],
      socket: null // 客户端和服务器端建立链接的socket对象
    }
  },
  created() {
    // 和axios无关，没有基地址
    this.socket = io('ws://geek.itheima.net', {
      query: {
        token: this.$store.state.user.token
      },
      transports: ['websocket']
    })
    // 绑定事件(测试是否建立成功)
    this.socket.on('connect', () => {
      console.log('成功')
    })
    // 接收后端传来事件
    this.socket.on('message', (obj) => {
      this.list.push({
        name: 'xs',
        msg: obj.msg
      })
      // 更新dom是异步，先执行主线程，所以打印的为自己发的，
      // const endDiv = document.querySelector('.chat-list>div:last-child')
      // console.log(endDiv)
    })
  },
  methods: {
    sendFn() {
      if (this.word.trim().length === 0) return
      // 发送消息的事件
      this.socket.emit('message', {
        msg: this.word,
        timestemp: new Date().getTime()
      })
      // 把消息显示到页面上
      this.list.push({
        msg: this.word,
        name: 'me'
      })
      // 清空输入框
      this.word = ''
    }
  },
  beforeDestroy() {
    this.socket.close()
    this.socket = null
  },
  watch: {
    list: {
      deep: true,
      handler(val) {
        // console.log(endDiv)
        // 更新dom是异步，先执行主线程，所以在真实dom放到页面之前就进行了滚动，这是不对的，应该是真实dom放上去之后再滚动，所以异步执行加nextTict
        this.$nextTick(() => {
          const endDiv = document.querySelector('.chat-list>div:last-child')
          endDiv.scrollIntoView({
            behavior: 'smooth'
          })
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
  background: #fafafa;
  padding: 80px 0 100px 0;
  .chat-list {
    height: 88vh;
    overflow-y: scroll;
    .chat-item {
      padding: 20px;
      .van-image {
        vertical-align: top;
        width: 80px;
        height: 80px;
      }
      .chat-pao {
        vertical-align: top;
        display: inline-block;
        min-width: 80px;
        max-width: 140%;
        min-height: 80px;
        line-height: 76px;
        border: 1px solid #c2d9ea;
        border-radius: 8px;
        position: relative;
        padding: 0 20px;
        background-color: #e0effb;
        word-break: break-all;
        font-size: 27px;
        color: #333;
        &::before {
          content: '';
          width: 20px;
          height: 20px;
          position: absolute;
          top: 24px;
          border-top: 1px solid #c2d9ea;
          border-right: 1px solid #c2d9ea;
          background: #e0effb;
        }
      }
    }
  }
}
.chat-item.right {
  text-align: right;
  .chat-pao {
    margin-left: 0;
    margin-right: 30px;
    &::before {
      right: -12px;
      transform: rotate(45deg);
    }
  }
}
.chat-item.left {
  text-align: left;
  .chat-pao {
    margin-left: 30px;
    margin-right: 0;
    &::before {
      left: -10px;
      transform: rotate(-135deg);
    }
  }
}
.reply-container {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 88px;
  width: 100%;
  background: #f5f5f5;
  z-index: 9999;
}
</style>
